<<template>
  <div class="login-container m-t-40 p-t-40 a-c">
    <h3 class="m-v-40">开发者入口</h3>
    <div flex="dir:left main:center cross:bottom">
      <vue-input type="password" label="个人口令" placeholder="请输入你的登陆口令" :value.sync="token" width="300" @onEnter="loginHandle"></vue-input>
      <el-button class="m-l-20" type="primary" size="small" @click="loginHandle">登入</el-button>
    </div>
  </div>
</template>

<script>
  import Toast from '@/toolkits/Toast'
  import VueInput from '../common/form/VueInput'
  import {Button} from 'element-ui'

  import {login} from '@/actions'

  export default {
    data () {
      return {
        token: ''
      }
    },
    methods: {
      loginHandle () {
        if (this.token) this.loginRemote()
        else Toast.show('请输入你的登录口令')
      },
      async loginRemote () {
        let result = await login(this.token)

        if (result) Toast.show('登入成功')
      }
    },
    components: {VueInput, 'el-button': Button}
  }
</script>

<style lang="scss" scoped>

</style>



