<template>
  <div class="p-t-40">
    <index-item v-for="item of articleData" :data="item" :key="item.title"></index-item>
  </div>
</template>

<script>
  import {getIndexArticle} from '@/actions'
  import IndexItem from './IndexItem'

  export default {
    data () {
      return {
        maxLength: 4,
        articleData: [
          {
            title: '最新文章',
            list: [
              {id: 123, title: '剖析 ajax 和未来的 fetch', tags: ['javaScript', 'javaScript', 'javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch657890123123', tags: ['javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch23786', tags: ['javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch', tags: ['javaScript'], date: '2017-01-02'}
            ]
          }, {
            title: '前端开发',
            list: [
              {id: 123, title: '剖析 ajax 和未来的 fetch', tags: ['javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch657890123123', tags: ['javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch', tags: ['javaScript'], date: '2017-01-02'}
            ]
          }, {
            title: '生活啊',
            list: [
              {id: 123, title: '剖析 ajax 和未来的 fetch', tags: ['javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch657890123123', tags: ['javaScript'], date: '2017-01-02'},
              {id: 123, title: '剖析 ajax 和未来的 fetch', tags: ['javaScript'], date: '2017-01-02'}
            ]
          }
        ]
      }
    },
    methods: {
      async getLatestArticle () {
        let data = await getIndexArticle(this.maxLength)
        this.articleData = data
      }
    },
    mounted () {
      this.getLatestArticle()
    },
    components: {
      IndexItem
    }
  }
</script>

<style lang="scss" scoped>
  .quote {
    font-size: 30px;
  }
</style>



