<template>
  <div class="m-v-40">
    <page-header zh="标签" en="Tags" title="文章涉及较多的知识点会形成文章标签"></page-header>
    <div class="m-v-30 p-h-20 font-16">
      <router-link class="m-h-10 p-10 iblock" v-for="item of categotyList" :key="item" :to="`/tags/${item}`">{{item}}</router-link>
    </div>
  </div>
</template>

<script>
  import PageHeader from '../common/PageHeader'

  export default {
    data () {
      return {
        categotyList: ['javascript', 'webGL', 'canvas', 'css3', 'HTML5', 'vue', 'react']
      }
    },
    components: {
      PageHeader
    }
  }
</script>

<style lang="scss" scoped>

</style>



