<template>
  <div class="p-20 c-fff bg-333 font-24">
    <div class="font-24 iblock" :title="title">{{zh}}<span class="font-14 m-l-8">{{en ? `/ ${en}` : ''}}</span></div>
  </div>
</template>

<script>
  export default {
    props: {
      zh: String,
      en: String,
      title: String
    }
  }
</script>

<style lang="scss" scoped>

</style>



